import * as React from 'react';
import reducer from './reducers';
import { DraggableDataProps } from './types';

const initData: DraggableDataProps = {};

export const DraggableContext = React.createContext<{
  dispatch: React.Dispatch<any>;
}>({
  dispatch: () => {},
});

export const useDraggable = () => {
  return React.useContext(DraggableContext);
};

export const useValue = () => React.useReducer(reducer, initData);
